.home {
    height: 92vh;
    background-image: -webkit-gradient(linear, left top, right bottom, from(rgba(126, 213, 111, 0.8)), to(rgba(40, 180, 133, 0.8))), url(../img/algorithm.jpeg);
    background-image: -webkit-linear-gradient(left top, rgba(126, 213, 111, 0.8), rgba(40, 180, 133, 0.8)), url(../img/algorithm.jpeg);
    background-image: -o-linear-gradient(left top, rgba(126, 213, 111, 0.8), rgba(40, 180, 133, 0.8)), url(../img/algorithm.jpeg);
    background-image: linear-gradient(to right bottom, rgba(126, 213, 111, 0.8), rgba(40, 180, 133, 0.8)), url(../img/algorithm.jpeg);
    background-size: 100% 100%;
    background-position: top;
    margin: 4vh;
    position: relative;
}

.title {
    position: absolute;
    top: 20%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.title h1 {
    text-align: center;
    color: #fff;
    font-size: 8rem;
    text-transform: uppercase;
    letter-spacing: 3.5rem;
    -webkit-animation: moveInTop 1s ease-out;
            animation: moveInTop 1s ease-out;
}

.card {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    
}

.linkBtn {
    text-decoration: none;
    background-color: #fff;
    padding: 1rem 2rem;
    font-size: 2rem;
    border-radius: 100px;
    display: block;
    position: relative;
    -webkit-animation: moveInBottom 1s ease-out .5s;
            animation: moveInBottom 1s ease-out .5s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
}

.linkBtn::after {
    content: "";
    display: block;
    height: 100%;
    width: 100%;
    background-color: #fff;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
}

.linkBtn:hover {
    -webkit-transform: translateY(-3px);
        -ms-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-box-shadow: 0 1rem 2rem rgba(77, 77, 82, 0.3);
            box-shadow: 0 1rem 2rem rgba(77, 77, 82, 0.3);
}

.linkBtn:hover::after {
    -webkit-transform: scale(1.5);
        -ms-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0;
}



.linkBtn:active {
    color: #000;
    -webkit-transform: translateY(-1px);
        -ms-transform: translateY(-1px);
            transform: translateY(-1px);
    -webkit-box-shadow: 0 .5rem 1rem rgba(77, 77, 82, 0.3);
            box-shadow: 0 .5rem 1rem rgba(77, 77, 82, 0.3);
}

@-webkit-keyframes moveInTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-5rem);
                transform: translateY(-5rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes moveInTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-5rem);
                transform: translateY(-5rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes moveInBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(5rem);
                transform: translateY(5rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes moveInBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(5rem);
                transform: translateY(5rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}


