.rect {
    position: absolute;
    width: 60px;
    height: 60px;
    border: 1px solid #000;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
}

.container {
    margin: 10vh auto 0 auto;
    width: 50%;
    height: 50%;
    position: relative;
}

.history {
    width: 50%;
    margin: 25px auto;
    position: relative;
}

.Button {
    width: 20%;
    height: 25px;
    margin: 0 2.5%;
    color: #fff;
    background-color: rgb(58, 117, 215);
    border: 0px;
    border-radius: 3px;
    cursor: pointer;
}

.answer {
    width: 10px;
    height: 10px;
    margin: 25px auto;
    background-color: red;
    border-radius: 5px;
    -webkit-animation: twinkling 1s infinite 0.5s ease-in-out alternate;
    animation: twinkling 1s infinite 0.5s ease-in-out alternate;
}

@-webkit-keyframes twinkling{   /*用于Chrome和Safari*/  
    0%{ 
        opacity:1;                /*透明度为0*/  
    }  
    100%{  
        opacity:0;              /*透明度为1*/  
    }  
}  
@keyframes twinkling{   /*用于IE，Firefox和Opera*/  
    0%{  
        opacity:1;                /*透明度为0*/  
    }  
    100%{  
        opacity:0;              /*透明度为1*/  
    }  
}  


